@import './components/layout/styleGlobal.css';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* nao conteudo nao ultrapassa a arear da div */
  font-family:  'Trebuchet MS', sans-serif;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  color: rgb(190, 190, 190);
}

body {
  background-image: url('imagens/back.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  overflow-y: scroll;
}

body::before {
  content: '';
  background-color: var(--color-blackshadow);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: fixed;
}


*::-webkit-scrollbar {
  width: 0;
}

* h1 {
  font-size: 90px;
}

* h2 {
  font-size: 70px;
}

* h3 {
  font-size: 50px;
}

* p {
  font-size: 20px;
}

@media screen and (max-width: 750px) {

  body {
    background-image: url('imagens/backSmall.jpg');
  }

  * h2 {
    font-size: 45px;
  }

  * h3 {
    font-size: 30px;
  }

  * p {
    font-size: 20px;
  }
}