:root {

    --primary-color: rgb(76, 162, 244);
    --primary-color-trans: rgb(76, 162, 244, .5);

    --secondary-color: rgb(2, 19, 35);
    --secondary-color-trans: rgb(2, 19, 35, .5);

    --accent-color: rgb(248, 78, 146);
    --accent-color-trans: rgb(248, 78, 146, .5);

    --color-white: rgb(255, 255, 255);
    --color-whiteshadow: rgb(255, 255, 255, .25);

    --color-black: rgb(0, 0, 0, 1);
    --color-blackshadow: rgb(55, 55, 55, .25)
}