@import '../layout/styleGlobal.css';

.btn {
    background-color: var(--secondary-color);
    color: white;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s linear;
    border-radius: 15px;
    
    box-shadow: 0px 0px 5px var(--color-whiteshadow),
        0px 0px 10px var(--color-whiteshadow),
        0px 0px 20px var(--color-whiteshadow);
}

.btn:hover {
    color: var(--color-black);
    background-color: var(--primary-color-trans);
}
