@import '../layout/styleGlobal.css';

.containerConta {
    width: 50%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title {
    width: 100%;
    margin-bottom: 60px;
}

.contacts {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.contacts div {
    display: flex;
    flex-direction: column;
    margin: 20px;
}

.contacts svg {
    font-size: 5em;
    cursor: pointer;
    margin: 20px;
    transition: 0.5s;
}

.contacts div a {
    text-decoration: none;
    color: var(--color-white);
    display: flex;
    justify-content: center;
    border-radius: 50%;
    box-shadow: 0px 0px 10px var(--color-whiteshadow),
        0px 0px 20px var(--color-whiteshadow),
        0px 0px 30px var(--color-whiteshadow),
        0px 0px 40px var(--color-whiteshadow);
}

.contacts div a:hover {
    box-shadow: 0px 0px 10px var(--secondary-color-trans),
        0px 0px 20px var(--secondary-color-trans),
        0px 0px 30px var(--secondary-color-trans),
        0px 0px 40px var(--secondary-color-trans);
    animation: spin 2s linear infinite;
}

.contacts svg:hover {
    color: var(--secondary-color-trans);
}

.inst a {
    background: linear-gradient(45deg, #f09433, #e6683c, #dc2743, #cc2366, #bc1888, #9012fe, #6b13d4, #3814a2, #051b80, #051b80);
}

.linkd a {
    background-color: rgb(0, 119, 181);
}

.gith a {
    background-color: rgb(111, 66, 193);
}

.whaspp a {
    background-color: rgb(37, 211, 102)
}

.email a {
    background: linear-gradient(45deg, rgb(255, 65, 54), rgb(255, 65, 54), rgb(255, 220, 0), rgb(0, 200, 83), rgb(0, 157, 224), rgb(63, 81, 181));
}

.contacts div span {
    /* color: red; */
    font-size: larger;
    font-weight: bold;
    margin-bottom: 15px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@media screen and (max-width: 750px) {

    * {
        margin: 0;
        padding: 0;
    }

    .containerConta {
        width: 70%;
        height: 100%;
    }

    .title {
        margin-bottom: 30px;
    }

    /* .contacts {
        flex-direction: column;
    } */

    .contacts div {
        margin: 10px;
    }

    .contacts svg {
        font-size: 2.8em;
    }

}