@import '../layout/styleGlobal.css';

.container {
    width: 1200px;
    height: 200px;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    flex-wrap: wrap;
    position: relative;
    top: 115px;
    /* background-color: var(--color-whiteshadow); */
    padding: 30px;
    overflow: auto;
    padding-bottom: 100px
}

.min_height {
    min-height: 80%;
}

.start {
    justify-content: flex-start;
}

.column {
    flex-direction: column;
    justify-content: flex-start;
}

@media screen and (max-width: 750px) {
    *{
        margin: 0;
        padding: 0;
    }
    .container{
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        align-content: center;

    }
    .min_height {
        min-height: 80%;
        
        justify-content: center;
        align-content: center;
    }
}