@import '../layout/styleGlobal.css';

.main {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    cursor: context-menu;
}

.about_me {
    margin: 0 15px;
    width: 70%;
    height: fit-content;
    border-radius: 20px;
    padding: 15px;
    text-align: center;

}

.main img {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    border: 2px solid var(--accent-color);
    background-color: var(--color-whiteshadow);
    transition: 1s;
    
    box-shadow: 0px 0px 8px var(--accent-color),
        0px 0px 16px var(--accent-color),
        0px 0px 24px var(--accent-color),
        0px 0px 32px var(--accent-color);
}

.img img:hover {
    border: 2px solid var(--primary-color);
    box-shadow: 0px 0px 8px var(--primary-color),
        0px 0px 16px var(--primary-color),
        0px 0px 24px var(--primary-color),
        0px 0px 32px var(--primary-color);

    background-color: var(--color-whiteshadow);
}

.divName {
    display: flex;
    justify-content: space-evenly;
    padding: 10px;
}

.divName h2 {
    transition: 1s;
}

.divName h2:hover {
    color: var(--accent-color);
}

.about_me h3 {
    padding: 25px 0;
}

.about_me p {
    padding: 20px 80px 20px 80px;
}

@media screen and (max-width: 750px) {
    
    .main {
        display: flex;
        flex-direction: column-reverse;
        position: relative;
        top: 20%;
    }

    .main img {
        width: 200px;
        height: 200px;
    }

    .about_me p {
        width: 100%;
        padding: 15px;
    }

    .divName {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding: 10px;
    }
}

.first {
    position: relative;
    left: -1000px;
    animation: slideIn 1.5s ease-out forwards;
}

.second {
    position: relative;
    top: +1000px;
    animation: slideDown 1.5s ease-out forwards;
}

.surname {
    position: relative;
    right: -1000px;
    animation: slideOut 1.5s ease-out forwards;
}

@keyframes slideIn {
    to {
        left: 0;
    }
}

@keyframes slideDown {
    to {
        top: 0;
    }
}

@keyframes slideOut {
    to {
        right: 0;
    }
}