@import '../layout/styleGlobal.css';

.navBar {
    display: flex;
    background-color: var(--secondary-color);
    justify-content: space-around;
    padding: 10px;
    font-size: larger;
    z-index: 999;
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    left: 0;
}

.list {
    display: flex;
    list-style: none;
    align-items: center;
}

.link {
    text-decoration: none;
    color: var(--primary-color);
    margin: .5em;
    padding: .5em;
    transition: .5s;
    border-radius: 10px;
}

.link:hover {
    color: var(--accent-color);
    background-color: var(--color-whiteshadow);
}

.navBar img {
    width: 60px;
    border-radius: 80px;
}

.navBar img:hover {    
    background-color: var(--color-blackshadow);
    box-shadow: 0px 0px 5px var(--color-whiteshadow),
        0px 0px 10px var(--color-whiteshadow),
        0px 0px 20px var(--color-whiteshadow);
}

@media screen and (max-width: 750px) {
    .navBar {
        padding: 1em .5em;
    }

    .social_list svg,
    .copy_right_span {
        font-size: 1em;
    }

}