@import '../layout/styleGlobal.css';

.footer {
    background-color: var(--secondary-color);
    color: var(--color-white);
    padding: 1em;
    text-align: center;
    z-index: 99999;

    position: fixed;
    width: 100%;
    bottom: 0;
    right: 0;
    left: 0;

}

.copy_right_span {
    font-weight: bold;
    color: var(--primary-color);
}

@media screen and (max-width: 750px) {
    .footer {
        display: none;
    }

}